.loading {
    position: absolute;
    width: 98vw;
    height: 97vh;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999;
    background-color: var(--background);
    overflow: hidden;

  }
  
  .load{
    position: absolute;
    border-radius: 100%; 
    height: 250px;
    width: 250px;
    overflow: hidden;
    align-items: center; 
    justify-content: center; 
  }
  
  .display{
    position: absolute;
    align-items: center; 
    justify-content: center; 
  }

  .swipe{
    position: fixed;
    width: 110vw;
    height: 110vh;
    background-color: var(--font-color);
  }