.project-page{
  .section-title{
    display: flex;
    flex-direction: column;
    align-content: start;
    margin: 2vw 0;
    h1{
      //margin: 0;
    }
  }
  .project-container {
    display: grid;
    width: 100%;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 5px 5px;
    justify-content: center;
    .project-item {
      padding: 20px;
      overflow: hidden;
      transition: 0.3s ease;
      filter: brightness(20%) contrast(50%);
      .image-container{
        display: flex;
        object-fit: cover;
        overflow: hidden;
        border-radius: 5px;
        img{
          filter: blur(4px);
        }
      }
      h3{margin: 0.5vw 0}    
      p{
        margin: 0; 
        transition: 0.3s ease-in-out;
      }
      &:hover{
        filter: brightness(100%);
        p{color: var(--red); }
        img{
          transform: scale(105%);
          filter: blur(0);
        }
      }
    }
  }
}


.project-main-image{
  transition: 0.3s ease-in-out;
}

.project-back-button{
    position: fixed;
    top: 100px;
    right: 0;
    margin: 20px;
    padding: 0px 10px;
    border: 2px solid;
    display: flex;
    text-transform: uppercase;
    background-color: transparent;
    transition: 0.3s ease-in-out;
    cursor: pointer;
    align-items: end;
    z-index: 3;
    backdrop-filter: blur(4px);
    h5{
        margin: 0;
        padding: 10px;
    }
    &:hover{
        h5{
            transform: translateY(-3px);
            color: var(--red);
        }
    }
}

.information{
  display: flex;
  grid-gap: 10px;
  justify-content: space-between;
  margin: 20px 0 0;
  height: max-content;
}

.information-text{
  position: relative;
  display:flex;
  flex-direction: column;
  backdrop-filter: blur(4px);
  z-index: 1;
}

.bento-box {
    display:grid;
    flex-direction: column;
    grid-gap: 10px;
    margin-right: 25px;
}

.side-bento{
    display: grid;
    flex-direction: column; 
    align-content: space-between;
    justify-content: space-between;
    transform: translateX();
  }

  .bento-container1, .bento-container2, .bento-container3{
    justify-content: center;
    overflow: hidden;
    //border: 1px solid var(--font-color);
    border-radius: 5px;
  }
  
  .bento-container1, .bento-container2{
    display: flex;
    align-content:center;
  }

  .bento-container1{
    width: 600px;
    height: 492px;
  }
  .bento-container2{
    width: 290px;
    height: 241px;
  }
  
  .bento-container3{
    display: grid;
    width: 900px;
    height: 241px;
  }

  @media (max-width: 1600px){
    .information{
      flex-direction: column;
      min-width: none;
      padding-right: 50px ;
      border-right: 1px solid var(--font-color);
    }
  }

  .information-text{
    min-width: none;
  }

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(2px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  position: relative;
  max-width: 90%;
  max-height: 90%;
}

.modal img {
  width: 100%;
  height: auto;
  border-radius: 10px;
}




















