.colourPanel{
    position: fixed;
    display:flex;
    bottom: 0;
    right: 0;
    backdrop-filter: blur(3px);
    margin: 0 2.4vw ;
}

.boxW, .boxB, .boxR {
    display: inline-block;
    width: 10px; 
    height: 10px; 
    margin-right: 10px; 
    cursor: pointer;
    border: 1px solid var(--font-color);
}

.boxW{background-color: #F4F6F6; }
.boxB {background-color: #1A1E23; }

.buttonC{
    margin-right: 40px;
}

.buttonCC,.buttonC{
    text-transform: uppercase;
    background-color: transparent;
    border: none;
    letter-spacing: 1px;
    white-space: nowrap;
    color: var(--font-color);
    font-weight: 100;
    cursor: pointer;
    transition: 0.3s ease-in-out;
    &:hover {
        transform: translateY(-3px);
        color: #CF3F3A;
    }
    ul{
        margin-right: 0;
    }
}

