.page{
    .title{
        display: flex;
        align-items: baseline;
    }
    .projects{
        display: grid;
        width: 100%;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 5px 5px;
        justify-content: center;
        @media (max-width:800px){
            grid-template-columns: 1fr;
        }
        .items {
            padding: 20px;
            overflow: hidden;
            transition: 0.3s ease;
            filter: brightness(20%) contrast(50%);
            .imageContainer{
                display: flex;
                object-fit: cover;
                overflow: hidden;
                border-radius: 5px;
                @media (max-width:800px){
                    width: 75vw;
                    height: 70vw;
                }
                img{
                    transition: 0.3s ease-in-out;
                    filter: blur(4px);
                }
            }
            h3{margin: 0.5vw 0}    
            p{
                margin: 0; 
                transition: 0.3s ease-in-out;
            }
            &:hover{
                filter: brightness(100%);
                p{color: var(--red); }
                img{
                    transform: scale(105%);
                    filter: blur(0);
                }
            }
        }
    }
}