.main-page{
    display: flex;
    width: 100%;
    height: calc((9 / 16) * 95vw);
    min-height: 350px;
    //grid-template-columns:50% 50%;
    //overflow: hidden;
    justify-content: center;
    align-items: center;
}

.main-name{
    font-size: 4.5vw;
    margin: 0;
    padding: 0;
    letter-spacing: 1.5px;
}

.first{font-size: 4.05vmin;}

.last{ 
    //font-size: 12.9vmin;
    font-size: 6.4vw;
    font-weight: 900;
    font-variant-numeric: slashed-zero ;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.main-right-side{
    display: grid;
    align-content: center;
    justify-content: center;
}

.main-left-side{
    display: flex;
    height: auto;
    width: 35%;
    justify-content: center;
    place-items: center;
    
    &:hover{
        .box{
            border: 2px solid var(--red);
        }
        .box1 {                     //Angel Top
            transform: translate(20%, -220%) rotate(-2deg);
        }
        
        .box2 {                     //Angel Bottom
            transform: translate(-190%, 30%) rotate(3deg);
        }
        
        .box3 {                     //Gone Wide
            transform: translate(80%, -260%) rotate(4deg);
        }

        .box4 {                     //Killua
            transform: translate(110%, 110%) rotate(1deg);
        }
        
        .box5 {                     //Donkey
            transform: translate(-100%, -120%) rotate(-2deg);
        }

        .box6{                     //Scream boy
            transform: scale(105%);
        }

        .view-projects{
            transform: translate(-10vw, 22vw) rotate(14deg);
        }
    }
}

.box{
    display: flex;
    position: absolute;
    overflow: hidden;
    border-radius: 10px;
    justify-content: center;
    align-content: center;
    transition: transform 800ms cubic-bezier(.05,.43,.25,.95);
    border: 2px solid var(--font-color);
    width: var(--box-width);
    img{
        object-fit: cover;    
    }
}

.box1 {                              //angel top
    //width: 15vmin;
    --box-width: 8.897143187vw;
    --aspect-ratio: 1 / 1;
    height: calc(var(--box-width) * (1 / var(--aspect-ratio)));
    //border: 3px solid pink;
    //transform: translate(30%, -125%) rotate(9deg);
    transform: translate(3vw, -11vw) rotate(9deg);
}

.box2 {                              //angel bottom
    //width: 15vmin;
    --box-width: 8.897143187vw;
    height: calc(7 / 5 * var(--box-width));
    //border: 3px solid pink;
    //transform: translate(-70%, 30%) rotate(9deg);
    transform: translate(-6.5vw, 7vw) rotate(9deg);
}

.box3 {                              //gon wide
    //width: 30vmin;
    --box-width: 17.79428637vw;
    height: calc(3 / 8 * var(--box-width));
    //border: 3px solid purple;
    //transform: translate(15%, -80%) rotate(-37deg);
    transform: translate(2.5vw, -5vw) rotate(-37deg);
}

.box4 {                              //killua
    //width: 25vmin;
    --box-width: 14.82857198vw;
    --aspect-ratio: 1 / 1;
    height: calc(var(--box-width) * (1 / var(--aspect-ratio)));
    //border: 3px solid yellow;
    //transform: translate(30%, 40%) rotate(-20deg);
    transform: translate(3vw, 7vw) rotate(-20deg);
}

.box5 {                              //donkey
    //width: 25vmin;
    --box-width: 14.82857198vw;
    height: calc( var(--box-width));
    //border: 3px solid blue;
    //transform: translate(-40%, -25%) rotate(-5deg);
    transform: translate(-3.7vw, -7.5vw) rotate(-7deg);
}

.box6{                              //scream boy
    //width: 35vmin;
    --box-width: 20.76vw;
    height: calc(7 / 5 * var(--box-width));
    //border: 3px solid red;
}

.view-projects{
    position: relative;
    font-size: calc(0.4vw + 6px);
    color: var(--font-color);
    border: 2px solid var(--font-color);
    padding: 0.5vw 1.2vw;
    background-color: transparent;
    margin: 0;
    cursor: pointer;
    align-items: end;
    transform: translate(0, 22vw);
    transition: transform 800ms cubic-bezier(.05,.43,.25,.95);

    h4{
        transition: 0.3s ease-in-out;
        margin: 0;
    }
    &:hover{
        
        h4{
        transform: translateY(-3px);
        color: var(--red);}
    }
}
