header{
    position: fixed;
    top: 0;
    display: flex;
    letter-spacing: 1px;
    flex-direction: column;
    font-size: 13px;
    z-index: 10;
    .container{
        display: flex;
        width: 100vw;
        padding: 20px 40px 5px 0px;
        justify-content: space-between;
        backdrop-filter: blur(5px);
        .titleContainer{
            display: flex;
            flex-direction: column;
            justify-content: center;
            .title{
                margin: 0;
                font-weight: 100;
                cursor: pointer;
                text-transform: uppercase;
                transition: 0.2s ease-in-out;
                mix-blend-mode: difference;
                &:hover{
                    color: var(--red);
                }
            }
            &:hover{
                flex-direction: column-reverse;
            }
        }
        .time{
            display: flex;
            justify-content: center;
            margin-bottom: 2px;
            color: var(--red);
            &:hover{
                color: unset;
            }
        }
        .buttonContainer{
            display: flex;
            align-content: center;
            .button{
                display: flex;
                border: none;
                text-transform: uppercase;
                background-color: transparent;
                margin: 0 10px 0;
                transition: 0.3s ease-in-out;
                cursor: pointer;
                align-items: end;
                color: var(--font-color);
                font-size: 13px;
                &.active {
                    text-decoration: line-through;
                    color: var(--red);

               }
                &:hover{
                    transform: translateY(-3px);
                    color: var(--red);
                }
            }
        }
    }
}

.progressBar {
    position: relative;
    height: 2px;
    background-color: var(--red); 
    margin-right: 20px;
}

@media (max-width:785px){
    header{
        .container{
            display: grid;
            flex-direction: column;
            justify-content: center;
            text-align: center;
            .time{
                display: none;
            }
            .buttonContainer{
                justify-content: center;
                .button{
                    margin: 0 1vw;
                }
            }
        }
    }
}


@media (max-width:370px){
    .titleContainer{
        h4{
            display: none;
        }
    }
}