.page{
    .title{
        display: flex;
        align-items: baseline;
        justify-content: end;
    }
    .skills{
        border-bottom: 1px solid var(--tertiary-color);
        width: 100%;
        height: 100%;
        .itemContainer{
            position: relative;
            display: flex;
            height: 100%;
            border-top: 1px solid var(--tertiary-color);
            margin: 0 5vw;
            overflow: hidden;
            .amount{
                position: absolute;
                height: 100%;
                width: 0;
                background-color: var(--red); 
                transition: width 0.3s cubic-bezier(.68,-0.55,.27,1.55);    
                z-index: -1; 
            }
            h2{
                height: 100%;
                font-size: 4vw;
                margin: 0;
                padding: 0.7vw;
            }
            p{
                display: none;
                margin-left: 3vw;
            }
            &:hover{
                p{
                    display: flex; 
                    color: var(--font-color);
                }
                .amount{
                    width: var(--skills-percentage);
                }
            } 
        }
        
    }
}
