footer{
    display: grid;
    height: calc(60vw * (2/5) + 190px + 2vw );
    width: 100%;
    position: sticky;
    bottom: 40px;
    align-content: end;
    background-color: var(--background);
    text-transform: uppercase;
    justify-items:right;
    margin-bottom: 2vw;
    .title{
        display: flex;
        align-items: end;
        h1{
            font-size: 13vw;
            text-align: end;
            margin-bottom: 0;
        }
        h8{
            font-size: calc(0.8vw + 8px);
        }
    }
    
    ul{
        list-style:decimal-leading-zero;
        a{
            padding: 10px 20px;
            text-decoration: none;
            color: var(--font-color);
            display: flex;
            &:hover{
                color: var(--red);
            }
        }
        
    }
    
    p{
        color: var(--red);
        justify-self: left;
    }
}