:root {
  --red: #CF3F3A;
}

* {
  box-sizing: border-box;
  cursor: not-allowed;
}

body{
  padding:1.2vw;
  --accent: var(--font-color);
  --accent1: var(--secondary-color);
  --accent2: var(--tertiary-color);
  --accent3: var(--quaternary-color);
  
  background-color: var(--background);
  color: var(--accent);
  
}


::-webkit-scrollbar {
  height: 5px;
  width: 5px;
  
  background: var(--background);
}::-webkit-scrollbar-thumb {
  background: var(--red);
  -webkit-border-radius: 1ex;
}

::-webkit-scrollbar-corner {
  background: var(--background);
}

html {
  scroll-snap-type: y mandatory;
  scroll-behavior: smooth;
}

.swipe{
    position: fixed;
    top: 0;
    left: 0;
    width: 110vw;
    height: 110vh;
    background-color: var(--font-color);
    z-index: 999;
}

h1, button{
  font-weight: 100;
  text-transform: uppercase;
}

h1{
  font-size: 10vmin;
}

h3{
  //font-size: calc(1.1111vmin + 8px);

}

h4{
}

h8{
  filter: brightness(60%);
}

p{
  font-size: calc(0.648148vmin + 9px);

}

